body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  margin-top:20px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: nonef;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.button {
    box-sizing: content-box;
    height: 24px;
    width:46px;
    background: #ffffff;
    border:1px solid rgab(153,153,153,1);
    padding:4px 1em;
    margin:10px;
    border-radius: 4px;
    position: relative;
    overflow: hidden;
}

.button:focus {
    outline: none;
    /* border: 1px solid black; */
}

.button .value {
    position: relative;
    z-index: 1;
}

.button .value.active {
    -webkit-animation: .6s changeFontColor;
            animation: .6s changeFontColor
}   

@-webkit-keyframes changeFontColor{
    0% {
        font-weight:normal;
    }
    100% {
        font-weight:700;
    }
}   

@keyframes changeFontColor{
    0% {
        font-weight:normal;
    }
    100% {
        font-weight:700;
    }
}

.button .circle {
    position: absolute;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background:#EEEEE0;
    display: inline-block;
    z-index: 0;
    -webkit-animation: .6s changeBig forwards;
            animation: .6s changeBig forwards;
}

@-webkit-keyframes changeBig{
    0% {
        -webkit-transform:scale(0.1);
                transform:scale(0.1);
    }
    100% { 
        -webkit-transform:scale(15); 
                transform:scale(15);
    };
}

@keyframes changeBig{
    0% {
        -webkit-transform:scale(0.1);
                transform:scale(0.1);
    }
    100% { 
        -webkit-transform:scale(15); 
                transform:scale(15);
    };
}
